  body {
    background: none !important;
  } 
  .search-box{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .login-box {
      margin: 30px;
    box-sizing: border-box;
    position: relative;
   padding:30px;
    background: rgba(135, 154, 179, 0.95);
    /* box-shadow: 0 15px 25px rgba(0,0,0,.6); */
    border-radius: 10px;
  }
  
  .login-box h2 {
    margin: 0 0 30px;
    padding: 0;
    color: white;
    font-family: 'Kameron';
    text-align: center;
  }
  
  .login-box .user-box {
    position: relative;
  }
  
  .login-box .user-box input {
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: #fff;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;
    background: transparent;
  }
  .login-box .user-box label {
    position: absolute;
    top:0;
    left: 0;
    padding: 10px 0;
    font-size: 24px;
    color: #fff;
    pointer-events: none;
    transition: .5s;
  }
  
  .login-box .user-box input:focus ~ label,
  .login-box .user-box input:valid ~ label {
    top: -20px;
    left: 0;
    color: #000000;
    font-size: 20px;
  }
  
  
  
  
  
  
 
  
  
  
  

  
